import { configureStore, createSlice } from "@reduxjs/toolkit";

let user = createSlice({
    name : 'user',
    initialState: 'kim'
})

let stock = createSlice({
    name : 'stock',
    initialState: [10, 11, 12]
})

let cart = createSlice({
    name: 'cart',
    initialState: [
      { id: 0, name: "Gibson Standard Leapaul 2013", count: 2 },
      { id: 1, name: "Gibson Standard Leapaul 2013", count: 4 },
      { id: 2, name: "Gibson Standard V", count: 11 }
    ],
    reducers: {
      increase(state, action) {
        let findIndex = state.findIndex((a) => a.id === action.payload);
        if (findIndex !== -1) {
          // Immer    
          state[findIndex].count += 1;
        }
      },
      addItem(state, action) {
        state.push(action.payload);
      }
    }
  });
  

export const { increase, addItem } = cart.actions;

export default configureStore({
    reducer: {
        user : user.reducer,
        stock : stock.reducer,
        cart : cart.reducer
    }
})