import './App.css';
import 'bootstrap/dist/css/bootstrap.min.css';
import { Container, Nav, Navbar, Row, Col } from 'react-bootstrap';
import data from './Data.js';
import { Suspense, lazy, useEffect, useState } from 'react';
import { Routes, Route, Link, useNavigate, useParams, Outlet } from 'react-router-dom';
import axios from 'axios';
import { useQuery } from 'react-query';

import AcousticGuitar from './pages/AcousticGuitar.js';
import ElectricGuitar from './pages/ElectricGuitar.js';
import Effects from './pages/Effects.js';
import Amps from './pages/Amps.js';


const Details = lazy(() => import('./pages/Details.js') );
const Cart = lazy(() => import('./pages/Cart.js') );


function App() {

  let [serverData, setServerData] = useState(data);
  let navigate = useNavigate();

  let [count, setCount] = useState(0);
  let [alert, setAlert] = useState(true);

  const onClick = () => {
    axios.get('http://localhost:8080/members/all')
      .then(reponse => {
        console.log(reponse.data)
      });
  }

  useEffect(() => {
    let a = setTimeout(() => { setAlert(false) }, 2000)

    return () => {
      clearTimeout(a)
    }
  }, [])

  useEffect(() => {
    localStorage.setItem('watched', JSON.stringify([]))
  }, [])

  let results = useQuery('', () =>
    axios.get('https://codingapple1.github.io/userdata.json')
      .then((a) => {
        return a.data
      })
  )

  return (
    <div className="App">
      {
        alert == true
          ? <div className='alert alert-warning'>
            
          </div>
          : null
      }


      <Navbar bg="white" data-bs-theme="">
        <Container>
          <Navbar.Brand href="/">ROKIN'</Navbar.Brand>
          <Nav className="me-auto">
            <Nav.Link href='/electricguitars'>Electric Guitars</Nav.Link>
            <Nav.Link href='/acousticguitars'>Acoustic Guitars</Nav.Link>
            <Nav.Link href='/effects'>Effects</Nav.Link>
            <Nav.Link href='/amps'>Amps</Nav.Link>
            <Nav.Link href='/cart'>Cart</Nav.Link>
          </Nav>
          <Nav className='ms-auto'>
            {results.data && results.data.name}
          </Nav>
        </Container>
      </Navbar>
      <button onClick={onClick}>
         
      </button>


      <Suspense fallback={<div></div>}>
        <Routes>
          <Route path='/' element={
            <>
              <div className='main-bg'>
              </div>
              <Row>
                {
                  serverData.map((a, i) => {
                    return (
                      <Detail i={i} serverData={serverData[i]}></Detail>
                    )
                  })
                }
              </Row>
              <Outlet />
            </>
          }>
            <Route path='detail/:id' element={<Details data2={serverData} />} />

          </Route>

          <Route path='/acousticguitars' element={<AcousticGuitar></AcousticGuitar>} />
          <Route path='/electricguitars' element={<ElectricGuitar></ElectricGuitar>} />
          <Route path='/effects' element={<Effects></Effects>} />
          <Route path='/amps' element={<Amps></Amps>} />
          <Route path='/cart' element={<Cart />} />

        </Routes>
      </Suspense>
    </div>
  );
}

function Detail(props) {
  return (
    <Col xs={6} md={4}>
      <Link to={`/detail/${props.i}`} style={{ color: 'black' }}>
        <img src={props.serverData.img} width="auto" height="300px"></img>
        <h4>{props.serverData.title}</h4>
        <p>{props.serverData.price}</p>
      </Link>
    </Col>
  )
}

export default App;
